* {
  box-sizing: border-box;
}

html, body {
  --border-radius: 100px;
  --blue: #1c47ff;
  --pink: #d6a3fb;
  --green: #d0ff94;
  --grey: #e5e5e5;
  --gutter: 3.125vw 6.25vw;
  color: #fff;
  background: #000;
  margin: 0;
  padding: 0;
  font-family: Syne, sans-serif;
  font-size: 20px;
}

h1 {
  margin: 0;
  font-size: 70px;
  line-height: 1em;
}

h2 {
  margin: 0;
  font-size: 60px;
}

a {
  color: #fff;
  text-decoration: none;
}

section {
  z-index: 2;
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  position: relative;
  top: 100px;
}

section.sticky {
  margin-top: 100px;
  position: sticky;
}

section.full-screen {
  z-index: 3;
  height: 100vh;
  margin-top: 0;
  top: 0;
}

nav {
  z-index: 1;
  width: 100%;
  height: 100px;
  font-size: 20px;
  position: fixed;
  top: 0;
  left: 0;
}

nav ul {
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin-left: 45px;
  list-style: none;
}

nav ul li a {
  color: #fff;
  font-family: Space Mono, serif;
}

section.about .empty {
  background: var(--grey);
  border-radius: var(--border-radius);
  flex-basis: 37.5vw;
  transition: flex-basis 1s cubic-bezier(0, .9, .4, .99);
}

section.about article {
  flex-direction: column;
  flex: 1;
  display: flex;
}

section.about article .title {
  background: var(--blue);
  border-radius: var(--border-radius);
  flex: 1;
  align-items: flex-end;
  padding: 3.125vw;
  transition: flex-basis 1s cubic-bezier(0, .9, .4, .99);
  display: flex;
}

section.about article .description {
  background: var(--pink);
  border-radius: var(--border-radius);
  color: #000;
  flex: 1;
  align-items: center;
  padding: 3.125vw;
  display: flex;
}

section.service article {
  width: 100%;
  display: flex;
}

section.service figure {
  background: var(--green);
  border-radius: var(--border-radius);
  flex-basis: 56.25vw;
  justify-content: center;
  align-items: center;
  max-width: 62.5vw;
  margin: 0;
  transition: flex-basis 1s cubic-bezier(0, .9, .4, .99);
  display: flex;
}

section.service figure img {
  width: 40%;
}

section.service .text {
  border-radius: var(--border-radius);
  background: #1f1a1c;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  padding: 3.125vw;
  display: flex;
}

section.service .text a {
  font-family: Space Mono, serif;
}

section.service.service-html figure img {
  animation-name: float;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes float {
  0% {
    transform: translateY(-5%);
  }

  100% {
    transform: translateY(5%);
  }
}

@keyframes scale {
  0% {
    transform: scale(none);
  }

  100% {
    transform: scale(.9);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(none);
  }

  100% {
    transform: rotate(360deg);
  }
}

section.service.service-css figure img {
  animation-name: scale;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

section.service.service-js figure img {
  animation-name: rotate;
  animation-duration: 15s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

section.service.service-html figure {
  background: var(--green);
}

section.service.service-html .text h2, section.service.service-html .text a {
  color: var(--pink);
}

section.service.service-css figure {
  background: var(--blue);
}

section.service.service-css .text h2, section.service.service-css .text a {
  color: var(--green);
}

section.service.service-js figure {
  background: var(--pink);
}

section.service.service-js .text h2, section.service.service-js .text a {
  color: var(--blue);
}

section.contact {
  background: linear-gradient(var(--blue), var(--green) 20%, var(--pink) 70%, var(--grey));
  border-radius: var(--border-radius);
  justify-content: center;
  align-items: center;
}

section.contact a {
  color: #000;
  will-change: filter;
  font-size: 100px;
  font-weight: bold;
  transition: filter 2s;
}

section.contact a:hover {
  filter: blur(20px);
}

@media only screen and (width <= 768px) {
  html, body {
    --border-radius: 50px;
    --gutter: 6.25vw;
  }

  h1 {
    font-size: 60px;
  }

  h2 {
    font-size: 40px;
  }

  nav ul li {
    margin-left: 25px;
  }

  section.service article {
    flex-direction: column;
  }

  section.service figure {
    flex-basis: 25vh;
  }

  section.service figure img {
    width: 20%;
  }

  section.contact a {
    font-size: 50px;
    transform: rotate(90deg);
  }
}
